import React from 'react';
import Card from '../card/Card';
import './Feedback.css';
class FeedbackList extends React.Component{
    reviewcards = this.props.feedback.map(fb=>{ return <Card id="feedback-item" title={fb.name} text={fb.review}></Card>});
    render()
    {

        return (<div className="feedback-container">{this.reviewcards}</div>)
    }
}

export default FeedbackList;