import React from 'react';
import "./Card.css"

class Card extends React.Component{
    render()
    {
        return (
    
            <div id={this.props.id} className="card">
            <h1 className="heading-card">{this.props.title}</h1>
            <p className="text-card">{this.props.text}</p>
            </div>
        )
    }
}

export default Card;